<style scoped>
	.Bimg {
		width: 100%;
		height: auto;
	}
	
	.rTitle {
		font-size: 20px;
		font-weight: bold;
		margin: 20px auto;
	}
	
	.xia {
		width: 35%;
		height: 1px;
		background: #707070;
		margin: 0 auto;
	}
	
	.rCon {
		font-size: 12px;
		text-align: left;
		text-indent: 2em;
		width: 95%;
		margin: 20px 10px;
	
	}
	
	.takeNum {
		width: 46%;
		height: 170px;
		background: white;
		margin: 0px auto;
		margin-top: 20px;
		box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
		opacity: 1;
		border-radius: 4px;
	}
	
	.takeCon {
		width: 90%;
		font-size: 13px;
		margin:0 10px;
	}
	
	.takeNum2 {
		width: 45%;
		height: 170px;
		background: white;
		margin: 0px auto;
		margin-top: 20px;
		border: 1px solid rgba(229, 229, 229, 1);
		border-radius: 4px;
	}
	
	.takeImg2 {
		width: 60px;
		height: 60px;
		margin-top: 30px;
	}
	
	.takeCon2 {
		height: 200px;
		font-size: 13px;
		width: 90%;
		margin: 30px 10px;
		text-align: left;
		color: #2c3e50;
	}
	
	.bottom {
		width: 100%;
		height: 2px;
	}
</style>


