<template>
	<div>
		<div>
			<img src="../../assets/lightbot/crowdFund2.jpg" style="width: 100%;height: auto">
		</div>
		<div>
			<p class="Dtitle"></p>
			<div class="xia"></div>
			<p class="Dfont">
				
			</p>
			<p class="Dfont">
				
			</p>
		</div>
		<div class="tese">
			<div class="Dtitle" style="padding-top:20px;"></div>
			<div class="xia"></div>
			<div class="warp" style="padding-top:20px;padding-bottom: 40px">
				<div>
					<img src="../../assets/lightbot/bright12.png" class="xImg">
					<p></p>
				</div>
				<div>
					<img src="../../assets/lightbot/bright13.png" class="xImg">
					<p></p>
				</div>
				<div>
					<img src="../../assets/lightbot/bright14.png" class="xImg">
					<p></p>
				</div>
				<div>
					<img src="../../assets/lightbot/bright15.png" class="xImg">
					<p></p>
				</div>
			</div>
		</div>
		<div>
			<img src="../../assets/lightbot/gongtongti.jpg" style="width: 85%;height: auto">
		</div>
		<div class="tese">
			<div class="Dtitle" style="padding-top:20px;"></div>
			<div class="xia"></div>
			<div class="Dfont" style="text-align: left;text-indent: 2em">
				
			</div>
			<div>
				<swiper :options="swiperOption" ref="mySwiper" class="swiper" style="height: 190px">
					<swiper-slide class="swiper_item">
						<img src="../../assets/lightbot/businessTwo1.png" class="pic"/>
					</swiper-slide>
					<swiper-slide class="swiper_item">
						<img src="../../assets/lightbot/businessTwo2.png" class="pic"/>
					</swiper-slide>
					<div class="swiper-pagination" slot="pagination" style="top:160px"></div>
				</swiper>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				swiperOption: {
					loop: true,
					autoplay: true,
					auto: 4000,
					pagination: {  //
						el: '.swiper-pagination',
					},
					autoplay: {//
						disableOnInteraction: false,
					}
				}
			}
		},
		created() {
			this.menu();
		},
		methods: {
			menu() {
				window.scrollTo(0, 0)
			}
		},
	}
</script>

<style scoped>
	.Pimg {
		width: 90%;
		height: auto;
		margin-top: 40px
	}
	
	.Dtitle {
		text-align: center;
		margin: 10px auto;
		font-size: 18px;
		font-weight: bold;
	}
	
	.Dfont {
		font-size: 12px;
		color: #666666;
		width: 90%;
		margin: 0 0 15px 20px;
		text-align: left;
		text-indent: 2em;
	}
	
	.xia {
		width: 30%;
		height: 1px;
		background: black;
		margin: 0px auto;
		margin-bottom: 20px;
	}
	
	.tese {
		width: 100%;
		height: auto;
		background: #F6F6F6;
	}
	
	.xImg {
		width: 50px;
		height: 50px;
		margin-bottom: 20px;
	}
</style>


