					autoplay: {
						disableOnInteraction: false,
					}
				},
			}
		},
		created(){
			this.common.menu();
		},
		methods: {
			selected(id) {
				this.selecId = id;
			},
			selecIntro(id) {
				this.introId = id;
			},
			selscene(id) {
				this.scene = id;
			},
			lookStatu(id) {
				let that = this;
				if (id == that.statu) {
					that.statu = 'ppp';
				} else {
					that.statu = id;
				}
			}
		},
		computed: {
			swiper() {
				return this.$refs.mySwiper.swiper
			}
