		text-align: center;
		margin: 15px auto;
		font-weight: bold;
	}
	
	.bjfont {
		background: #F67524;
		color: white;
		border: 1px solid white;
	}
	
	.addImg {
		width: 95%;
		height: auto;
		margin: 15px auto;
	}
	
	.intro {
		font-size: 12px;
		text-align: left;
		text-indent: 2em;
		margin: 0px 10px;
	}
</style>


