<template>
	<div class="form">
		<div class="Ftitle"></div>
		<div class="xia"></div>
		<!---->
		<div class="warp" style="position: relative">
			<div class="wbody" style="border-bottom: 4px solid #1EDCDC;" @click="showTime(0)">
				<div><img src="../../assets/restaurant/organization1.png" class="fimg"></div>
				<div class="fFont"></div>
			</div>
			<div class="wbody" style="border-bottom: 4px solid #4FA3DA;" @click="showTime(1)">
				<div><img src="../../assets/restaurant/organization2.png" class="fimg"></div>
				<div class="fFont"></div>
			</div>
			<div class="wbody" style="border-bottom: 4px solid #E94A40;" @click="showTime(2)">
				<div><img src="../../assets/restaurant/organization3.png" class="fimg"></div>
				<div class="fFont"></div>
			</div>
			<div class="wbody" style="border-bottom: 4px solid #FED387;" @click="showTime(3)">
				<div><img src="../../assets/restaurant/organization4.png" class="fimg"></div>
				<div class="fFont"></div>
			</div>
			<div class="layer posi1" v-if="status==0" @click="showTime(0)">
				<div><img src="../../assets/restaurant/organizations1.png" class="layImg"></div>
				<div class="layTitle"></div>
				<div class="layContent"></div>
			</div>
			<div class="layer posi2" v-if="status==1" @click="showTime(1)">
				<div><img src="../../assets/restaurant/organizations2.png" class="layImg"></div>
				<div class="layTitle"></div>
				<div class="layContent"></div>
			</div>
			<div class="layer posi3" v-if="status==2" @click="showTime(2)">
				<div><img src="../../assets/restaurant/../../assets/restaurant/organizations3.png" class="layImg"></div>
				<div class="layTitle"></div>
				<div class="layContent"></div>
			</div>
			<div class="layer posi4" v-if="status==3" @click="showTime(3)">
				<div><img src="../../assets/restaurant/organizations4.png" class="layImg"></div>
				<div class="layTitle"></div>
				<div class="layContent"></div>
			</div>
		</div>
		<div class="Ftitle"></div>
		<div class="xia"></div>
		<div class="warp">
			<div class="stat">
				<div><img src="../../assets/catering/count1.png" class="Timg"></div>
				<div class="Ttitle"></div>
				<p class="Tcontent">
					
				</p>
			</div>
			<div class="stat">
				<div><img src="../../assets/catering/count2.png" class="Timg"></div>
				<div class="Ttitle"></div>
				<p class="Tcontent">
					
				</p>
			</div>
			<div class="stat">
				<div><img src="../../assets/catering/count3.png" class="Timg"></div>
				<div class="Ttitle"></div>
				<div class="Tcontent">
					</div>
			</div>
			<div class="stat">
				<div><img src="../../assets/catering/count4.png" class="Timg"></div>
				<div class="Ttitle"></div>
				<div class="Tcontent">
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "form",
		data() {
			return {
				status: '555s'
			}
		},
		methods: {
			showTime(id) {
				var that=this;
				if(that.status==id){
					that.status='ppp';
				}else{
					that.status = id;
					
				}
			}
		}
	}
</script>
<style scoped>
	.Ftitle {
		font-size: 20px;
		font-weight: bold;
		margin: 15px auto;
	}
	
	.xia {
		width: 30%;
		height: 1px;
		background: #707070;
		margin: 0 auto;
	}
	
	/**/
	.wbody {
		/*overflow: hidden;*/
		position: relative;
		width: 40%;
		height: 220px;
		border: 1px solid #E5E5E5;
		margin: 10px auto;
		cursor: pointer;
	}
	
	.fBody {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
	}
	
	.fimg {
		width: 40px;
		height: 40px;
		margin: 40px auto;
	}
	
	.fFont {
		font-size: 17px;
		color: #666666;
	}
	
	/**/
	.stat {
		width: 45%;
		height: 220px;
		margin: 10px auto;
		border: 1px solid #ddd;
		box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
		position: relative;
	}
	
	.Timg {
		width: 100%;
		height: auto;
	}
	
	.Tcontent {
		font-size: 12px;
		margin: 10px 5px;
		color: #666666;
		text-align: left;
	}
	
	.Ttitle {
		position: absolute;
		top: 18%;
		left: 35%;
		color: white;
		z-index: 49;
	}
	
	.layer {
		width: 40%;
		z-index: 50;
		height: 220px;
		margin: 10px auto;
		cursor: pointer;
		color: white;
	}
	
	.posi1 {
		position: absolute;
		top: 0;
		left: 18px;
		background: #1EDCDC;
	}
	
	.posi2 {
		position: absolute;
		top: 0;
		left: 55%;
		background: #4FA3DA;
	}
	
	.posi3 {
		position: absolute;
		top: 50%;
		left: 18px;
		background: #E94A40;
	}
	
	.posi4 {
		position: absolute;
		top: 50%;
		left: 55%;
		background: #FED387;
	}
	
	.layImg {
		width: 50px;
		height: 50px;
		margin: 0 auto;
		margin-top: 10px;
	}
	
	.layTitle {
		margin: 8px auto;
		font-size: 15px;
		text-align: center;
	}
	
	.layContent {
		font-size: 11px;
		width: 90%;
		margin: 0px 10px;
		text-align: left;
	}
</style>


