<template>
	<div>
		<div><img src="../../assets/underline/special1.jpg" class="bimg"></div>
		<div class="Htitle"></div>
		<div class="xia"></div>
		<div>
			<img src="../../assets/underline/station.png" class="Himg">
		</div>
		<!--1-->
		<div class="huibj">
			<div class="Htitle" style="padding-top: 20px;"></div>
			<div class="xia" style="margin-bottom: 20px"></div>
			<div class="warp">
				<div class="wscope" v-for="item in contentInfo">
					<div><img :src="item.imgs" class="scopeImg"></div>
					<div class="scopeFont">{{item.title1}}</div>
					<div class="scopeCon">{{item.title2}}</div>
				</div>
			</div>
		</div>
		<!--2-->
		<div style="padding-bottom: 20px">
			<div class="Htitle" style="padding-top: 20px;"></div>
			<div class="xia" style="margin-bottom: 20px"></div>
			<div class="bttenWarp">
				<div class="wscope" v-for="item in mode">
					<div><img :src="item.imgs" class="scopeImg"></div>
					<div class="scopeFont">{{item.title1}}</div>
					<div class="scopeCon">{{item.title2}}</div>
				</div>
			</div>
		</div>
		<!--3-->
		<div class="huibj">
			<div class="Htitle" style="padding-top: 20px;"></div>
			<div class="xia" style="margin-bottom: 20px"></div>
			<swiper :options="swiperOption" ref="mySwiper" class="swiper" style="height: 260px">
				<swiper-slide v-for="(item,index) in flowInfo" :key="index" class="swiper_item">
					<div class="wscope2">
						<div><img :src="item.imgs" class="scopeImg"></div>
						<div class="scopeFont">{{item.title1}}</div>
						<div class="xia2" :style="{background:item.colors}"></div>
						<div class="scopeCon" style="text-align: center">{{item.title2}}</div>
					</div>
				</swiper-slide>
				<div class="swiper-pagination" slot="pagination"></div>
			</swiper>
		</div>
		<!--4-->
		<div>
			<div class="Htitle" style="padding-top: 20px;margin-top: 0"></div>
			<div class="xia" style="margin-bottom: 20px"></div>
			<div class="xiaoshou" v-for="item in sell">
				<div class="flexs">
					<div class="flexs" style="width: auto;">
						<div><img :src="item.imgs" class="fImg"></div>
						<div class="fTitle" style="justify-content: center;font-size: 13px">{{item.title1}}</div>
					</div>
				</div>
				<div class="xcontent" style="font-size: 11px">{{item.title2}}</div>
			</div>
			<div class="xiaoshou">
				<div class="flexs">
					<div class="flexs" style="width: auto;">
						<div><img src="../../assets/underline/under3.png" class="fImg" style="width: 26px;height: 20px">
						</div>
						<div class="fTitle" style="justify-content: center;font-size: 13px"></div>
					</div>
				</div>
				<div class="xcontent" style="font-size: 11px"></div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "offineIndex",
		data() {
			return {
				contentInfo: [
					{
						imgs: require('../../assets/underline/revolution1.png'),
						title1: '',
						title2: ''
					},
					{
						imgs: require('../../assets/underline/revolution2.png'),
						title1: '',
						title2: ''
					},
					{
						imgs: require('../../assets/underline/revolution3.png'),
						title1: '',
						title2: ''
					},
					{
						imgs: require('../../assets/underline/revolution4.png'),
						title1: '',
						title2: ''
					}
				],
				mode: [
					{
						imgs: require('../../assets/underline/obtain1.png'),
						title1: 'WiFi',
						title2: 'WIFI'
					},
					{
						imgs: require('../../assets/underline/obtain2.png'),
						title1: '',
						title2: 'WIFI'
					},
					{
						imgs: require('../../assets/underline/obtain3.png'),
						title1: '',
						title2: 'AA'
					},
					{
						imgs: require('../../assets/underline/obtain4.png'),
						title1: '',
						title2: ''
					}
				],
				flowInfo: [
					{
						imgs: require('../../assets/underline/canversion1.png'),
						title1: '',
						title2: '',
						colors: '#F5B51C'
					},
					{
						imgs: require('../../assets/underline/canversion2.png'),
						title1: '',
						title2: '',
						colors: '#4FA3DA'
					},
					{
						imgs: require('../../assets/underline/canversion3.png'),
						title1: '',
						title2: '',
						colors: '#E72F6F'
					},
					{
						imgs: require('../../assets/underline/canversion4.png'),
						title1: '',
						title2: '',
						colors: '#35B794'
					},
					{
						imgs: require('../../assets/underline/zhixiao.png'),
						title1: '',
						title2: 'APP',
						colors: "#ED594D"
					}
				],
				sell: [
					{
						imgs: require('../../assets/underline/under1.png'),
						title1: '',
						title2: ''
					},
					{
						imgs: require('../../assets/underline/under2.png'),
						title1: '',
						title2: ''
					}
				],
				swiperOption: {
					loop: true,
					autoplay : 2000,
					pagination: {
						el: '.swiper-pagination',
					},
					autoplay: {
						disableOnInteraction: false,
					}
				},
			}
		},
		created(){
			this.common.menu()
		},
		computed: {
			swiper() {
				return this.$refs.mySwiper.swiper
			}
		},
	}
</script>

<style scoped>
	.bimg {
		width: 100%;
		height: auto;
	}
	
	.Htitle {
		margin: 20px auto;
		font-size: 20px;
		font-weight: bold;
	}
	
	.xia {
		background: #707070;
		width: 40%;
		height: 1px;
		margin: 0 auto;
	}
	
	.Himg {
		width: 100%;
		background: #F4F4F4;
		height: auto;
		margin: 20px auto;
	}
	
	.huibj {
		background: #F6F6F6;
		width: 100%;
		height: auto;
	}
	
	.wscope {
		width: 45%;
		height: 200px;
		background: #FFFFFF;
		margin: 10px auto;
		box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
		border-radius: 4px;
	}
	
	.wscope2 {
		width: 90%;
		height: 210px;
		background: #FFFFFF;
		margin: 10px auto;
		box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
		border-radius: 4px;
	}
	
	.scopeImg {
		margin-top: 30px;
		margin-bottom: 5px;
		width: 60px;
		height: 60px;
	}
	
	.scopeFont {
		font-size: 14px;
		font-weight: bold;
		text-align: center;
		width: 95%;
		margin: 5px 10px;
	}
	
	.scopeCon {
		font-size: 12px;
		width: 95%;
		margin: 5px 5px;
		text-align: center;
	}
	
	.xia2 {
		width: 20%;
		height: 1px;
		margin: 15px auto;
	}
	
	.flexs {
		width: 100%;
		background: #F67524;
		height: 50px;
		margin: 0 auto;
	}
	
	.fImg {
		width: 22px;
		height: 22px;
		margin: 13px 0 0 10px;
	}
	
	.fTitle {
		margin: 15px 0 0 10px;
		color: white;
	}
	
	.xiaoshou {
		width: 92%;
		height: auto;
		margin: 10px auto;
		border-radius: 4px;
		box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
	}
	
	.xcontent {
		margin: 20px auto;
		font-size: 14px;
		width: 90%;
		text-align: left;
		padding-bottom: 10px;
	}

</style>


